import "./pages/index.css";
import { initialCards } from "./scripts/cards.js";
import { deleteCard, createCard } from "./components/card.js";
import {
  openPopup,
  closePopup,
  closePopupOverlay,
} from "./components/modal.js";
export { cardTemplate, openImageHandler };

// @todo: Темплейт карточки
const cardTemplate = document.querySelector("#card-template").content;

// @todo: DOM узлы
const cardsContainer = document.querySelector(".places__list");

// @todo: Вывести карточки на страницу
initialCards.forEach((card) => {
  const createdCard = createCard(card, deleteCard, openImageHandler);
  cardsContainer.appendChild(createdCard);
});

// Открытие и закрытие модального окна
const buttonOpenPopupProfile = document.querySelector(".profile__edit-button");
const buttonOpenPopupNewCard = document.querySelector(".profile__add-button");
const popupEdit = document.querySelector(".popup_type_edit");
const popupNewCard = document.querySelector(".popup_type_new-card");

buttonOpenPopupProfile.addEventListener("click", () => openPopup(popupEdit));
buttonOpenPopupNewCard.addEventListener("click", () => openPopup(popupNewCard));

popupEdit.addEventListener("click", (evt) => closePopupOverlay(evt, popupEdit));
popupNewCard.addEventListener("click", (evt) =>
  closePopupOverlay(evt, popupNewCard)
);

const hidePopupImg = document.querySelector(".popup_type_image ");
hidePopupImg.addEventListener("click", (evt) =>
  closePopupOverlay(evt, hidePopupImg)
);



// Изменение имени
const formElement = document.querySelector(".popup__form");
const nameInput = document.querySelector(".popup__input_type_name");
const jobInput = document.querySelector(".popup__input_type_description");
const nameElement = document.querySelector(".profile__title");
const jobElement = document.querySelector(".profile__description");

function handleEditFormSubmit(evt) {
  evt.preventDefault();

  const name = nameInput.value;
  const job = jobInput.value;

  // Отправка запроса на сервер методом PATCH
  fetch('https://nomoreparties.co/v1/wff-cohort-8/users/me', {
    method: 'PATCH',
    headers: {
      authorization: 'f0900dc3-fecd-4246-b516-514bd8cfc01c',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      name: name,
      about: job
    })
  })
  .then((res) => {
    if (!res.ok) {
      throw new Error(`Ошибка при редактирование данных профиля: ${res.status}`);
    }
    return res.json();
  })
  .then((data) => {
    // Если обновление прошло успешно, обновляем данные на странице
    nameElement.textContent = data.name;
    jobElement.textContent = data.about;

    console.log('Изменено')
    
    // Закрываем попап
    closePopup(popupEdit);
  })
  .catch((error) => {
    console.error("Произошла ошибка при редактирование данных профиля:", error);
  });
}

formElement.addEventListener("submit", handleEditFormSubmit);


// Открытие иллюстрации
const popupTypeImage = document.querySelector(".popup_type_image");
const popupImageContent = popupTypeImage.querySelector(
  ".popup__content_content_image"
);
const popupImage = popupImageContent.querySelector(".popup__image");
const popupCaption = document.querySelector(".popup__caption");

// Функция добавления новой карточки
const popupNewCardForm = popupNewCard.querySelector(".popup__form");
const newCardNameInput = popupNewCard.querySelector(
  ".popup__input_type_card-name"
);
const newCardLinkInput = popupNewCard.querySelector(".popup__input_type_url");

function addNewCard(event) {
  event.preventDefault();
  const newCard = {
    name: newCardNameInput.value,
    link: newCardLinkInput.value,
  };

  fetch('https://nomoreparties.co/v1/wff-cohort-8/cards', {
    method: 'POST',
    headers: {
      authorization: 'f0900dc3-fecd-4246-b516-514bd8cfc01c',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(newCard)
  })
  .then(response => {
    if (!response.ok) {
      throw new Error('Не удалось добавить карточку');
    }
    return response.json();
  })
  .then(data => {
    const createdCard = createCard(data, deleteCard, openImageHandler);
    cardsContainer.insertBefore(createdCard, cardsContainer.firstChild);
    closePopup(popupNewCard);
    event.target.reset();
    console.log('Test')
  })
  .catch(error => {
    console.error('Ошибка при добавлении карточки:', error);
  });
}

popupNewCardForm.addEventListener("submit", addNewCard);

function openImageHandler(src, alt, caption) {
  openPopup(popupTypeImage);
  popupImage.src = src;
  popupImage.alt = alt;
  popupCaption.textContent = caption;
}


// Валидация формы
const formInput = document.querySelectorAll(".popup__input");
const formError = formElement.querySelector(`.${formInput.id}-error`);
const submitButton = document.querySelectorAll(".popup__button");

const showError = (input, errorMessage) => {
  const errorElement = input.nextElementSibling;
  errorElement.classList.add("form__input-error_active");
  errorElement.textContent = errorMessage;
  submitButton.forEach((button) => {
    button.setAttribute("disabled", "true");
    button.classList.add("popup__button-inactive");
  });
};

const hideError = (input) => {
  const errorElement = input.nextElementSibling;
  errorElement.classList.remove("form__input-error_active");
  errorElement.textContent = "";
  submitButton.forEach((button) => {
    button.removeAttribute("disabled", "true");
    button.classList.remove("popup__button-inactive");
  });
};

const isValid = (input) => {
  if (input.validity.patternMismatch) {
    input.setCustomValidity(input.dataset.errorMessage);
  } else {
    input.setCustomValidity("");
  }

  if (!input.validity.valid) {
    showError(input, input.validationMessage);
  } else {
    hideError(input);
  }
};

formElement.addEventListener("submit", function (evt) {
  evt.preventDefault();
  formInput.forEach((input) => isValid(input));
});

formInput.forEach((input) => {
  input.addEventListener("input", function () {
    isValid(input);
  });
});

// Работа с API

// Загрузка информации о пользователе и карточек с сервера
Promise.all([
  fetch("https://nomoreparties.co/v1/wff-cohort-8/users/me", {
    headers: {
      authorization: "f0900dc3-fecd-4246-b516-514bd8cfc01c",
    },
  }),
  fetch("https://nomoreparties.co/v1/wff-cohort-8/cards", {
    headers: {
      authorization: "f0900dc3-fecd-4246-b516-514bd8cfc01c",
    },
  }),
])
  .then(([userRes, cardsRes]) => Promise.all([userRes.json(), cardsRes.json()]))
  .then(([userInfo, initialCards]) => {
    console.log("Информация о пользователе:", userInfo);
    console.log("Начальные карточки:", initialCards);
  })
  .catch((error) => {
    console.error("Error:", error);
  });


